import React, { Component } from 'react'
import {
  Card,
  Input,
  Icon,
  Form,
  Button,
  Cascader,
  message
} from 'antd'

import LinkButton from '../../components/link-button'
import RichTextEditor from './rich-text-editor'
import PicturesWall from './pictures-wall'
import { reqCategorys, reqAddOrUpdateProduct } from '../../api'

const { Item } = Form
const { TextArea } = Input

class ProductAddUpdate extends Component {
  state = {
    options: []
  }

  constructor(props) {
    super(props)
    this.pw = React.createRef()
    this.editor = React.createRef()
  }

  initOptions = async (categorys) => {
    // options
    const options = categorys.map(c => ({
      value: c._id,
      label: c.name,
      isLeaf: false,
    }))

    const { product, isUpdate } = this
    const { pCategoryId } = product
    //  
    if (isUpdate && pCategoryId !== '0') {
      // 
      const subCategorys = await this.getCategorys(pCategoryId)
      // options
      const childrenOption = subCategorys.map(c => ({
        value: c._id,
        label: c.name,
        isLeaf: true
      }))
      // options
      const targetOption = options.find(option => option.value === pCategoryId)
      // options
      targetOption.children = childrenOption
    }

    // 
    this.setState({options})
  }

  getCategorys = async (parentId) => {
    const result = await reqCategorys(parentId)
    if (result.status === 0) {
      const categorys = result.data
      if (parentId === '0') {
        this.initOptions(categorys)
      } else {
        return categorys
      }
    }
  } 

  submit = () => {
    this.props.form.validateFields( async (err, values) => {
      if (!err) {
        const imgs = this.pw.current.getImgs()
        const detail = this.editor.current.getDetail()
        const { name, desc, price, categoryIds } = values
        let categoryId, pCategoryId
        if (categoryIds.length === 1) {
          pCategoryId = '0'
          categoryId = categoryIds[0]
        } else {
          pCategoryId = categoryIds[0]
          categoryId = categoryIds[1]
        }
        const product = { name, desc, price, pCategoryId, categoryId, imgs, detail }
        if (this.isUpdate) {
          product._id = this.product._id
        }
        const result = await reqAddOrUpdateProduct(product)
        if (result.status === 0) {
          message.success(`${this.isUpdate ? '' : ''}`)
          this.props.history.goBack()
        } else {
          message.error(`${this.isUpdate ? '' : ''}`)
        }
      }
    })
  }

  validatorPrice = (rule, value, callback) => {
    if (!value) {
      callback('')
    } else if (value * 1 < 0) {
      callback('0')
    } else {
      callback()
    }
  }

  loadData = async selectedOptions => {
    const targetOption = selectedOptions[0]
    targetOption.loading = true
    const subCategorys = await this.getCategorys(targetOption.value)
    targetOption.loading = false
    if (subCategorys && subCategorys.length > 0) {
      const childrenOption = subCategorys.map(c => ({
        value: c._id,
        label: c.name,
        isLeaf: true,
      }))
      targetOption.children = childrenOption
    } else {
      targetOption.isLeaf = true
    }
    this.setState({
      options: [...this.state.options],
    })
  }

  componentDidMount () {
    this.getCategorys('0')
  }

  componentWillMount () {
    // debugger
    const product = this.props.location.state
    this.isUpdate = !!product //
    this.product = product || {}
  }

  render() {
    // debugger
    const { product, isUpdate } = this
    const { categoryId, pCategoryId, imgs, detail } = product
    const { getFieldDecorator } = this.props.form
    const categoryIds = []
    if (isUpdate) {
      if (pCategoryId === '0') {
        categoryIds.push(categoryId)
      } else {
        categoryIds.push(pCategoryId)
        categoryIds.push(categoryId)
      }
    }
    const formItemLayout = {
      labelCol: { span: 2 },
      wrapperCol: { span: 8 },
    }
    const title = (
      <span>
        <LinkButton>
          <Icon 
            type='arrow-left' 
            style={{marginRight: 10}}
            onClick={() => this.props.history.goBack()}
          />
        </LinkButton>
        <span>{isUpdate ? '' : ''}</span>
      </span>
    )
    return (
      <Card title={title}>
        <Form {...formItemLayout}>
          <Item label=''>
            {
              getFieldDecorator('name', {
                initialValue: product.name,
                rules: [{ required: true, message: '!' }]
              })(<Input placeholder=''></Input>)
            }
          </Item>
          <Item label=''>
            {
              getFieldDecorator('desc', {
                initialValue: product.desc,
                rules: [{ required: true, message: '!' }]
              })(<TextArea placeholder="" autosize={{ minRows: 2, maxRows: 6 }} />)
            }
          </Item>
          <Item label=''>
            {
              getFieldDecorator('price', {
                initialValue: product.price,
                rules: [
                  { required: true, validator: this.validatorPrice },
                ]
              })(<Input type='number' placeholder='' addonAfter=''></Input>)
            }
          </Item>
          <Item label=''>
            {
              getFieldDecorator('categoryIds', {
                initialValue: categoryIds,
                rules: [
                  { required: true, message: '!' },
                ]
              })(
                <Cascader
                  placeholder=''
                  options={this.state.options}
                  loadData={this.loadData}
                />
              )
            }
          </Item>
          <Item label=''>
            <PicturesWall ref={this.pw} imgs={imgs}/>
          </Item>
          <Item label='' labelCol={{span: 2}} wrapperCol={{span: 20}}>
            <RichTextEditor ref={this.editor} detail={detail}/>
          </Item>
          <Item>
            <Button type='primary' onClick={this.submit}></Button>
          </Item>
        </Form>
      </Card>
    )
  }
}

export default Form.create()(ProductAddUpdate)

