import React, { Component } from 'react'
import {
  Card,
  Button,
  Icon,
  Table,
  message,
  Modal
} from 'antd'
import LinkButton from '../../components/link-button'
import AddFrom from './add-from'
import UpdateFrom from './update-from'

import { reqCategorys, reqAddCategory, reqUpdateCategory } from '../../api'

export default class Category extends Component {
  state = {
    categorys: [],
    loading: false,
    subCategorys: [], // 
    parentId: '0',
    categoryName: '' ,
    showStatus: 0
  }

  getCategorys = async (parentId) => {
    parentId = parentId || this.state.parentId
    this.setState({loading: true})
    const result = await reqCategorys(parentId)
    if (result.status === 0) {
      this.setState({loading: false})
      const categorys = result.data
      if (parentId === '0') {
        this.setState({
          categorys
        })
      } else {
        this.setState({
          subCategorys: categorys
        })
      }
    } else {
      message.error('')
    }
  }

  showSubCategorys = (categorys) => {
    this.setState({
      parentId: categorys._id,
      categoryName: categorys.name
    }, () => {
      this.getCategorys()
    })
  }

  showCategorys = () => {
    this.setState({
      parentId: '0',
      subCategorys: [],
      categoryName: ''
    })
  }

  handleCancel = () => {
    // 
    this.form.resetFields()
    this.setState({
      showStatus: 0
    })
  }

  showAdd = () => {
    this.setState({
      showStatus: 1
    })
  }

  addCategory = () => {
    this.form.validateFields( async (err, values) => {
      if (!err) {
        const { parentId, categoryName } = values
        // 
        this.form.resetFields()
        // 
        this.setState({
          showStatus: 0
        })
        const result = await reqAddCategory(parentId, categoryName)
        if (result.status === 0) {
          // 
          if (parentId === this.state.parentId) {
            this.getCategorys()        
          } else if (parentId === '0') {
            this.getCategorys('0')
          }
        }
      }
    })
  }

  showUpdate = (categorys) => {
    this.category = categorys
    this.setState({
      showStatus: 2
    })
  }

  updateCategory = async() => {
    this.form.validateFields( async (err, values) => {
      if (!err) {
        const categoryId = this.category._id
        const categoryName = values.categoryName
        // 
        this.form.resetFields()
        // 
        this.setState({
          showStatus: 0
        })
        const result = await reqUpdateCategory({categoryId, categoryName})
        if (result.status === 0) {
          // 
          this.getCategorys()
        }
      }
    })
  }

  initColumns = () => {
    this.columns = [
      {
        title: '',
        dataIndex: 'name',
      },
      {
        title: '',
        width: 300,
        render: (row) => (
          <span>
            <LinkButton onClick={() => this.showUpdate(row)}></LinkButton>
            { this.state.parentId === '0' ? <LinkButton onClick={() => this.showSubCategorys(row)}></LinkButton> : ''}
          </span>
        )
      }
    ]
  }
  // render
  componentWillMount() {
    this.initColumns()
  }
  // 
  componentDidMount() {
    this.getCategorys()
  }

  render() {
    const { categorys, loading, parentId, categoryName, subCategorys, showStatus } = this.state
    const category = this.category || {}
    const title =  parentId === '0' ? '' : (
      <span>
        <LinkButton onClick={this.showCategorys}></LinkButton>
        <Icon type='arrow-right' style={{marginRight: 5}}></Icon>
        <span>{categoryName}</span>
      </span>
    )
    const extra = (
      <Button type='primary' onClick={this.showAdd}>
        <Icon type='plus' />
        
      </Button>
    )
    
    return (
      <Card title={title} extra={extra}>
        <Table 
          bordered 
          loading={loading}
          pagination={{
            defaultPageSize: 5,
            showQuickJumper: true
          }}
          dataSource={ parentId === '0' ? categorys : subCategorys} 
          rowKey='_id'
          columns={this.columns} />

        <Modal
          title=""
          visible={ showStatus === 1 }
          okText=''
          cancelText=''
          onOk={this.addCategory}
          onCancel={this.handleCancel}
        >
          <AddFrom 
            categorys={categorys}
            parentId={parentId}
            setForm={(form) => this.form = form}
          />
        </Modal>

        <Modal
          title=""
          okText=''
          cancelText=''
          visible={ showStatus === 2 }
          onOk={this.updateCategory}
          onCancel={this.handleCancel}
        >
          <UpdateFrom 
           categoryName={category.name}
           setForm={(form) => this.form = form}
          />
        </Modal>

      </Card>
    )
  }
}
