import React, { PureComponent } from 'react'
import {
  Card,
  Table,
  Modal,
  Button,
  message
} from 'antd'
import LinkButton from '../../components/link-button'
import UserFrom from './user-form'
import { PAGE_SIZE } from '../../utils/const.js'
import { formateDate } from '../../utils/dateUtils'
import { reqUsers, reqDelUser, reqAddOrUpdateuser } from '../../api'
 
export default class User extends PureComponent {
  state = {
    users: [],
    user: {}, // user
    roles: [],
    isShow: false
  }

  getUsers = async () => {
    const result = await reqUsers()
    if (result.status === 0) {
      const {users, roles} = result.data
      this.initRoleNames(roles)
      this.setState({
        users,
        roles
      })
    }
  }

  initRoleNames = (roles) => {
    const roleNames = roles.reduce((pre, role) => {
      pre[role._id] = role.name
      return pre
    }, {})
    this.roleNames = roleNames
  }

  initColumns = () => {
    this.columns = [
      {
        title:'',
        dataIndex: 'username'
      },
      {
        title:'',
        dataIndex: 'email'
      },
      {
        title:'',
        dataIndex: 'phone'
      },
      {
        title:'',
        dataIndex: 'create_time',
        render: formateDate
      },
      {
        title:'',
        dataIndex: 'role_id',
        render: (role_id) => this.roleNames[role_id]
      },
      {
        title:'',
        render: (user) => (
          <span>
            <LinkButton onClick={() => this.setState({isShow: true, user})}></LinkButton>
            <LinkButton onClick={() => this.delUser(user)}></LinkButton>
          </span>
        )
      },
    ]
  }

  addOrUpdateUser = () => {
    this.form.validateFields (async (err, values) => {
      if (!err) {
        // console.log(values)
        if (this.state.user._id) {
          values._id = this.state.user._id
        }
        const result = await reqAddOrUpdateuser(values)
        if(result.status === 0) {
          message.success(`${this.state.user._id ? '' : ''}`)
          this.setState({isShow: false})
          this.form.resetFields()
          this.getUsers()
        } else {
          message.error('')
        }
      }
    })
  }

  delUser = (user) => {
    Modal.confirm({
      title: '',
      content: `${user.username}?`,
      okText: '',
      cancelText: '',
      onOk: async () => {
        const result = await reqDelUser(user._id)
        if(result.status === 0) {
          message.success('')
          this.getUsers()
        } else {
          message.error('')
        }
      }
    })
  }

  componentWillMount () {
    this.initColumns()
  }

  componentDidMount () {
    this.getUsers()
  }

  render() {
    const { users, isShow, roles, user } = this.state
    const title = <Button type='primary' onClick={() => this.setState({isShow: true, user: {}})}></Button>

    return (
      <Card title={title}>
        <Table 
          bordered
          rowKey='_id'
          dataSource={users} 
          columns={this.columns}
          pagination={{defaultPageSize: PAGE_SIZE}}
        />
        <Modal
          title={user._id ? '' : ''}
          visible={ isShow }
          okText=''
          cancelText=''
          onOk={this.addOrUpdateUser}
          onCancel={
            () => {
              this.setState({
                isShow: false
            })
            this.form.resetFields()
          }}
        >
          <UserFrom 
            roles={roles}
            user={user}
            setForm={(form) => this.form = form}
          />
        </Modal>
      </Card>
    )
  }
}
