import React, { Component } from 'react'
import { Modal } from 'antd'
import { withRouter } from 'react-router-dom'
import menuList from '../../config/menuConfig'
import './index.less'
import { reqWeather } from '../../api/index'
import memoryUtils from '../../utils/memoryUtils.js'
import storeUtils from '../../utils/storeUtils'
import { formateDate } from '../../utils/dateUtils'
import LinkButton from '../../components/link-button'

const confirm = Modal.confirm

class Header extends Component {
  state = {
    sysTime: formateDate(Date.now()),
    dayPictureUrl: '',
    weather: '',
    currentCity: ''
  }
  getTime = () => {
    this.setIntervalId = setInterval(() => {
      const sysTime = formateDate(Date.now())
      this.setState({sysTime})
    }, 1000)
  }
  getTitle = () => {
    let path = this.props.location.pathname
    let title
    menuList.forEach(item => {
      if (item.key === path || path.indexOf(item.key) === 0) {
        title = item.title
      } else if (item.children) {
        const cItem = item.children.find(cItem => cItem.key === path || path.indexOf(cItem.key) === 0)
        if (cItem) {
          title = cItem.title
        }
      }
    })
    return title
  }
  loginOut = () => {
    confirm({
      title: '',
      content: '',
      okText: '',
      cancelText: '',
      onOk: () => {
        // user
        memoryUtils.user = {}
        storeUtils.removeUser()
        // 
        this.props.history.replace('/login')
      }
    })
  }
  getWeathInfo = async () => {
    const { dayPictureUrl, weather, currentCity } = await reqWeather('')
    this.setState({
      dayPictureUrl,
      weather,
      currentCity
    })
  }

  async componentWillMount() {
    this.getWeathInfo()
    this.getTime()
  }
  // 
  componentWillUnmount() {
    // 
    clearInterval(this.setIntervalId)
  }

  render() {
    const { dayPictureUrl, weather, sysTime, currentCity } = this.state
    const userName = memoryUtils.user.username
    const title = this.getTitle()
    return (
      <div className='header'>
        <div className='header-top'>
          <span>{userName}</span>
          <LinkButton onClick={this.loginOut}></LinkButton>
        </div>
        <div className='header-bottom'>
          <div className='header-bottom-left'>
            {title}
          </div>
          <div className='header-bottom-right'>
            <span>{sysTime}</span>
            <img src={dayPictureUrl} alt={weather}/>
            <span>{currentCity}{weather}</span>
          </div>
        </div>
      </div>
    )
  }
}

export default withRouter(Header)